.panel-body {
  position: relative;
  overflow: hidden;
  clear: both;
}

/***************************************/

/************ PANEL DEFAULT ************/

/***************************************/

/* Panel Up */
.panel-default-overlay-up {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f5f5f5;
  color: #333;
  visibility: hidden;
  opacity: 0;
  margin: 0 auto;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}
.panel-body.overlayed .panel-default-overlay-up {
  visibility: visible;
  opacity: 1;
  -moz-transform: translateY(-100%);
  -o-transform: translateY(-100%);
  -webkit-transform: translateY(-100%);
  transform: translateY(-100%);
}

/* Panel Down */
.panel-default-overlay-down {
  position: absolute;
  top: -100%;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f5f5f5;
  color: #333;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}
.panel-body.overlayed .panel-default-overlay-down {
  visibility: visible;
  opacity: 1;
  -moz-transform: translateY(100%);
  -o-transform: translateY(100%);
  -webkit-transform: translateY(100%);
  transform: translateY(100%);
}

/* Panel Left */
